import React from 'react';
import styles from './BasicText.css';

export default class BasicText extends React.Component {
    static propTypes = {
        data: React.PropTypes.array.isRequired,
        index: React.PropTypes.number.isRequired,
        color: React.PropTypes.string.isRequired
    }

    render () {
        const { data, index, color } = this.props;
        function createCopyMarkupForH1() { return {__html: `${data[index].headline}`}; }
        function createCopyMarkupForP() { return {__html: `${data[index].copy}`}; }
        return (
            <div className={`${styles['container']} ${styles[color]}`}>
                <hr className={`${styles['spacer']}`} />
                <h1 dangerouslySetInnerHTML={createCopyMarkupForH1()}></h1>
                <p dangerouslySetInnerHTML={createCopyMarkupForP()}></p>

            </div>
        );
    }
}


