import React from 'react';
import styles from './stickynav.css';
import TweenMax from 'gsap';

export default class Stickynav extends React.Component {

    static propTypes = {
        handleClick: React.PropTypes.func.isRequired,
        allAnchors: React.PropTypes.array.isRequired,
        selectedAnchorPoint: React.PropTypes.number.isRequired,
    }
    //sticky nav stick control
    stickynavPositionUpdate = (pageYOffset, margin) => {
        var that = this;
        if(pageYOffset < this.stickyPosition + this.navHeight + margin){
            if(this.timer !== null) {
                clearTimeout(this.timer);
            }
            this.emptyNode.style.display = 'none';
            this.node.style.position = 'relative';
            this.node.style.top = 0;

        }
        else if(pageYOffset >= this.stickyPosition + this.navHeight + margin){
            this.emptyNode.style.display = 'block';
            this.node.style.position = 'absolute';
            this.node.style.top = pageYOffset - 9999;
            if(this.timer !== null) {
                clearTimeout(this.timer);
            }
            this.timer = setTimeout(function() {
                that.node.style.top = pageYOffset - margin;
                that.node.style.position = 'absolute';
                TweenMax.fromTo(that.node, 0.5,
                    {y: 0, opacity: 0.1},
                    {y: 0, opacity: 1}
                );
            }, 300);
        }
        for (let i = 0; i < this.props.allAnchors.length; i++) {
                this.list.children[i].style.boxShadow = 'inset 0px 0px 0px 0px';
                if(i === 0 && pageYOffset - margin < this.props.allAnchors[i] - this.navHeight){
                    this.list.children[i].style.boxShadow = 'inset 0px 0px 0px 0px';
                    return;
                }
                if(i === this.props.allAnchors.length - 1 && pageYOffset - margin >= this.props.allAnchors[i] - this.navHeight){
                    this.list.children[i-1].style.boxShadow = 'inset 0px 0px 0px 0px';
                    this.list.children[i].style.boxShadow = 'inset 0px -2px 0px 0px #00adef';
                    return;
                }
                if(pageYOffset - margin >= this.props.allAnchors[i] - this.navHeight && pageYOffset - margin <= this.props.allAnchors[i+1] - this.navHeight){
                    if(i > 0){
                        this.list.children[i-1].style.boxShadow = 'inset 0px 0px 0px 0px';
                    }
                    this.list.children[i].style.boxShadow = 'inset 0px -2px 0px 0px #00adef';
                }
        }
    }

    receiveMessage = (event) => {
        this.stickynavPositionUpdate(event.data, 117);
    }

    handleScroll = () => {
        this.stickynavPositionUpdate(window.pageYOffset, 0);
    }

    //sticky nav scrolling
    scrollTo = (element, duration) => {
        var e = document.documentElement;
        if (e.scrollTop === 0) {
            var t = e.scrollTop;
            ++e.scrollTop;
            e = t + 1 === e.scrollTop-- ? e : document.body;
        }
        this.scrollToC(e, e.scrollTop, element, duration);
    }

    scrollToC = (element, from, to, duration) => {
        if (duration <= 0) return;

        if (typeof from === "object") from = from.offsetTop;
        if (typeof to === "object") to = to.offsetTop;

        this.scrollToX(element, from, to, 0, 1 / duration, 20, this.easeInOutCuaic);
    }

    scrollToX = (element, xFrom, xTo, t01, speed, step, motion) => {
        let that = this;
        if (t01 < 0 || t01 > 1 || speed <= 0) {
            element.scrollTop = xTo;
            return;
        }
        element.scrollTop = xFrom - (xFrom - xTo) * motion(t01);
        t01 += speed * step;

        setTimeout(function () {
            that.scrollToX(element, xFrom, xTo, t01, speed, step, motion);
        }, step);
    }

    easeInOutCuaic = (t) => {
        t /= 0.5;
        if (t < 1)return t * t * t / 2;
        t -= 2;
        return (t * t * t + 2) / 2;
    }

    // width control
    updateDimensions = () => {

        let w = window,
            d = document,
            documentElement = d.documentElement,
            body = d.getElementsByTagName('body')[0],
            newWidth = w.innerWidth || documentElement.clientWidth || body.clientWidth;

        if(this.node && newWidth < 1000){
            this.node.style.width = newWidth - 15;
        } else if(this.node && newWidth >= 1000 ){
            this.node.style.width = 1000;
        }

    }
    componentWillMount() {
        this.updateDimensions();
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions);
    }

    componentDidMount(){
        this.navHeight = 60;
        window.addEventListener('resize', this.updateDimensions);
        let url = (window.location != window.parent.location)
            ? document.referrer
            : document.location.href;
        this.urlTest = url.includes('www.mercedes-benz.co.uk');
        if (this.urlTest){
            this.timer = null;
            window.addEventListener('message', this.receiveMessage);
        } else {
            window.addEventListener('scroll', this.handleScroll);
        }
        setTimeout(() => {
            this.stickyPosition = this.node.offsetTop;
        }, 1000);
    }

    render(){
        let anchorData = {
            type: 'anchor',
        };
        if(this.urlTest){
            anchorData.anchorPoint = this.props.selectedAnchorPoint - this.navHeight + 117;
            parent.window.postMessage(anchorData, '*');
        } else {
            this.scrollTo(this.props.selectedAnchorPoint - this.navHeight, 1200);
        }
        return(
            <div>
                <div className={`${styles['empty-node']}`} ref={(emptyNode => this.emptyNode = emptyNode)}></div>
                <div ref={(node) => this.node = node}  className={`${styles['lost-container']}`}>
                    <div className={`${styles['lost-heading']}`}>
                        <h3>Finance</h3>
                    </div>
                    <div className={`${styles['lost-ul']}`}>
                        <ul ref={(list) => this.list = list} className={`${styles['stickynav']}`}>
                            <li id="0" onClick={this.props.handleClick.bind(this)} >Anchor 1</li>
                            <li id="1" onClick={this.props.handleClick.bind(this)} >Anchor 2</li>
                            <li id="2" onClick={this.props.handleClick.bind(this)} >Anchor 3</li>
                            <li id="3" onClick={this.props.handleClick.bind(this)} >Anchor 4</li>
                            <li id="4" onClick={this.props.handleClick.bind(this)} >Anchor 5</li>
                        </ul>
                    </div>
                <hr className={`${styles['nav-border']}`} />
                </div>
            </div>
        );
    }
}




