import React from 'react';
import Slider from 'react-slick';
import Image from './image.jsx';
import Copy from './copy.jsx';
import styles from './textimagecarousel.css';

export default class Textimagecarousel extends React.Component {
    static propTypes = {
        data: React.PropTypes.array.isRequired,
        index: React.PropTypes.number.isRequired,
        color: React.PropTypes.number.isRequired,
        reverse: React.PropTypes.number.isRequired
    }
    render(){
        const { data, index, color, reverse} = this.props;
        let settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1
        };
        let reverseClass;
        if(reverse){
            reverseClass = 'reverse';
        }
        let colorClass;
        if (color){
            colorClass = 'black';
        }
        return(
            <div className={`${styles['lost-container']} ${styles[reverseClass]} ${styles[colorClass]}`}>
                <div className={`${styles['lost-50']} ${styles['tablet-lost-100']} ${styles['img-container']}`}>
                    <Slider {...settings} afterChange={this.beforeChange} >
                        {data[index].img.map((element, index) => {
                            return (
                                <div key={index}>
                                    <Image imgSrc={element}/>
                                </div>
                            );
                        })}
                    </Slider>
                </div>
                <div className={`${styles['lost-50']} ${styles['tablet-lost-100']} ${styles['lost-center']} ${styles['copy-container']}`}>
                    <Copy title={data[index].title} copy={data[index].copy} url={data[index].url}/>
                </div>
            </div>
        );
    }
}


