import React from 'react';
import Carousel from './components/carousel/carousel.jsx';
import Stickynav from './components/stickynav/stickynav.jsx';
import BasicText from './components/BasicText/BasicText.jsx';
import TextImageCarousel from './components/textimagecarousel/textimagecarousel.jsx';
import BasicTextData from './components/BasicText/data1.jsx';
import TextImageCarouselData from './components/textimagecarousel/data.jsx';

export default class App extends React.Component {
    constructor(){
        super();
        this.nodes = new Map();
        this.state = {
            allAnchors: [],
            selectedAnchorPoint: 0,
            activeAnchor: 0
        };
    }
    componentDidMount(){
        setTimeout(() => {
            this.computeAnchors();
        }, 1000);
    }

    computeAnchors = (e) => {
        let anchorArray = [];
        Array.from(this.nodes.values())
            .filter(node => node != null)
            .forEach((node, index) => {
                anchorArray = [...anchorArray, node.offsetTop];
                if(e && e.target.id == index){
                    this.setState({
                        ...this.state,
                        selectedAnchorPoint: node.offsetTop,
                        activeAnchor: index
                    });
                }
            });
        this.setState({
            allAnchors: anchorArray
        });
    }

    handleClick = (e) => {
        e.stopPropagation();
        this.computeAnchors(e);
    }

    render(){
        return(
            <div className={`grid-container grid-parent`}>
                <Carousel />
                <Stickynav handleClick={this.handleClick} {...this.state} />
                <div ref={anchorPoint => this.nodes.set(0, anchorPoint)} />
                <BasicText data={BasicTextData} index={0} color={'black'} />
                <div ref={anchorPoint => this.nodes.set(1, anchorPoint)} />
                <TextImageCarousel data={TextImageCarouselData} index={0} color={0} reverse={0} />
                <div ref={anchorPoint => this.nodes.set(2, anchorPoint)} />
                <TextImageCarousel data={TextImageCarouselData} index={0} color={1} reverse={1} />
                <div ref={anchorPoint => this.nodes.set(3, anchorPoint)} />
                <TextImageCarousel data={TextImageCarouselData} index={0} color={0} reverse={0} />
                <div ref={anchorPoint => this.nodes.set(4, anchorPoint)} />
                <TextImageCarousel data={TextImageCarouselData} index={0} color={1} reverse={1} />
                <TextImageCarousel data={TextImageCarouselData} index={0} color={0} reverse={0} />
                <BasicText data={BasicTextData} index={0} color={'black'} />
                <TextImageCarousel data={TextImageCarouselData} index={0} color={0} reverse={1} />
            </div>
        );
    }
}




