import React from 'react';

export default class Image extends React.Component {
    static propTypes = {
        imgSrc: React.PropTypes.string.isRequired,
    }
    render(){
        const { imgSrc } = this.props;
        return(
                <img src={imgSrc} width="100%" />
        );
    }
}


